@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  /* 57bd84 */
  --color-brand: #ff1d1d;
  --color-brand-light: #ff1d1d;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
  background-color: #f5f5f5;
  height: 100vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
  font-family: sans-serif;
}

.widget {
  display: flex;
  width: 650px;
  flex-wrap: wrap;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0 0 25px #00000023;
}

.widget .weatherIcon {
  flex-basis: 100%;
  padding: 25px 10px 35px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fafafa;
  font-family: weathericons;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 100px;
}

.widget .widget-mid {
  display: flex;
  align-items: stretch;
  width: 100%;
}

.widget .weatherInfo {
  flex-basis: 70%;
  padding: 25px 10px;
  background: #080705;
  display: flex;
  align-items: center;
  color: white;
}
.widget .weatherInfo .temperature {
  flex: 0 0 40%;
  width: 100%;
  font-size: 65px;
  display: flex;
  justify-content: space-around;
  margin-right: 20px;
}
.widget .weatherInfo .description {
  flex: 0 60%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
}
.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 100;
}
.widget .weatherInfo .description .place {
  font-size: 15px;
}
.widget .date {
  flex-basis: 30%;
  background: var(--color-brand);
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
}

.widget .extra-temp {
  flex: 1 100%;
  padding: 5% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  font-size: 100px;
}

.widget .extra-temp p {
  font-size: 25px;
  font-weight: 600;
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  color: var(--color-brand);
  /* background-color: blanchedalmond; */
}

.widget .extra-temp .extra-info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
}

/* input search button style  */

.search {
  width: 100%;
  margin-top: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.searchTerm {
  width: 300px;
  border: none;
  padding: 16px;
  height: 45px;
  border-radius: 2px 0 0 2px;
  outline: none;
  border: 1px solid #ebebeb;
  font-size: 18px;
}

.searchTerm::placeholder {
  color: #bebebe;
}

.searchTerm:focus {
  color: #222;
}

.searchButton {
  width: 60px;
  height: 43px;
  border: none;
  background: var(--color-brand-light);
  text-align: center;
  color: #fff;
  border-radius: 0 2px 2px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
  font-size: 16px;
}

.searchButton:hover {
  background-color: var(--color-brand);
}

.wrap {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: -150px;
  margin-bottom: 50px;
}

/*Resize the wrap to see the search bar change!*/
