} */

@media (max-width: 500px) {
  #root {
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
  }
  body {
    display: block;
    background: #fff;
  }
  .wrap {
    margin: 0 0 30px;
  }
  .search {
    padding: 15px;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  .searchTerm {
    width: 100%;
    font-size: 20px;
    height: unset;
    border: 1px solid #bebebe;
  }
  .searchButton {
    height: unset;
    font-size: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 15px 0;
    margin-top: 20px;
    background: #ff1d1d;
    color: #fff;
  }
  .widget {
    width: 100%;
    box-shadow: unset;
  }
  .widget-mid {
    flex-direction: column;
  }
  .date {
    padding: 20px 0;
  }
  .widget .extra-temp {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  .widget .extra-temp .temp-info-minmax,
  .widget .extra-temp .weather-extra-info {
    padding: 15px 0;
  }
}
